import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PrepaidCardComponent } from './prepaid-card.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';

import { RouterModule, Routes } from '@angular/router';
import { ConfigPrintComponent } from './config-print/config-print.component';
import { AccountInformationComponent } from './account-information/account-information.component';

const routes: Routes = [
  {
    path: '',
    component: PrepaidCardComponent, 
  }
]
@NgModule({
  declarations: [
    PrepaidCardComponent,ConfigPrintComponent,AccountInformationComponent
  ],
  imports: [
    CommonModule, 
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule,
    FormsModule,
  ]
})
export class PrepaidCardModule { }
