import { Router } from '@angular/router';
import {
  ChangeDetectorRef,
  Component,
  OnInit,
} from '@angular/core';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from '../components/vh-component/vh-component';
import { LanguageService } from '../services/language.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { TranslateService } from '@ngx-translate/core';
import { VhSaleInvoicesExchangePaymentCard } from '../interface/vh-sale-payment-card-invoice';
import { ExchangePaymentCardK57Col3Component } from '../components/print/prepaid-card/exchange-payment-card-k57-col3/exchange-payment-card-k57-col3.component';
import { ExchangePaymentCardK80Col3Component } from '../components/print/prepaid-card/exchange-payment-card-k80-col3/exchange-payment-card-k80-col3.component';
import { ExchangePaymentCardA4Component } from '../components/print/prepaid-card/exchange-payment-card-a4/exchange-payment-card-a4.component';
import { ExchangePaymentCardK80Col4Component } from '../components/print/prepaid-card/exchange-payment-card-k80-col4/exchange-payment-card-k80-col4.component';
import { ExchangePaymentCardA5Component } from '../components/print/prepaid-card/exchange-payment-card-a5/exchange-payment-card-a5.component';
import { VhBillType51Add } from '../components/bill-type/bill-type-51/bill-type51-interface';
import { HttpClient } from '@angular/common/http';
import { ThemeService } from '../services/theme.service';

export interface TreeNodeInterface {
  key: string;
  name: string;
  code?: number;
  level?: number;
  expand?: boolean;
  price?: number;
  children?: TreeNodeInterface[];
  parent?: TreeNodeInterface;
  products: Array<any>;
  unit: string;
  ratio: number;
  quantity: number;
  restquantity: number;
  quantity_use: number;
  stock: number;
  id_payment_card_code?: string;
  outquantity: number;
  id_lotproduct?: string;
  lots?: Array<any>;
  name_payment_card? :string;
  name_payment_card_release? : string;
}

@Component({
  selector: 'app-prepaid-card',
  templateUrl: './prepaid-card.component.html',
  styleUrls: ['./prepaid-card.component.scss'],
})
export class PrepaidCardComponent implements OnInit {
  public user: any = this.vhAuth.getUser(); // thng tin ti khon hin ti
  public modalAccountInfo: boolean = false; // show/hide moal thng tin ti khon
  currentDate: Date = new Date();
  code: string = '';
  heightScroll: string = '';
  list_products: any[] = [];
  product_codes: any[] = [];
  showConfigPrint = false;
  showConfig = false;
  public langList: any[] = [
    {
      name: 'Vietnamese',
      value: 'vi',
      img: 'assets/icon/settings/language/vi.svg',
    },
    {
      name: 'English',
      value: 'en',
      img: 'assets/icon/settings/language/en.svg',
    },
  ];
  public lang: string = this.vhAuth.localStorageGET('language_code');
  size: any = [12, 13, 14, 15, 16, 17, 18, 19, 20];
  customer: any;
  list_payment_cards: any[] = [];
  templates: { name: string; url: string; giai_thich: string; }[] = [];
  store_main_sector = '';
  country_code = 'VN';
  language = 'vi';

  constructor(
    public vhQuerySales: VhQuerySales,
    public vhAuth: VhAuth,
    public vhAlgorithm: VhAlgorithm,
    public vhComponent: VhComponent,
    public languageService: LanguageService,
    public router: Router,
    public cdRef: ChangeDetectorRef,
    private translateService: TranslateService,
    private http: HttpClient,
    private modal: NzModalService,
    public themeService: ThemeService,
  ) { }

  ngOnInit(): void {
    this.store_main_sector = this.vhAuth.getStore().main_sector; // Ly main_sector ca ca hng
    this.http.get(`assets/documents/configuration/template/${this.country_code}/${this.store_main_sector}.json`).subscribe((templates) => {
      this.templates = templates[this.language];
    }); // Ly giao din theo main_sector ca ca hng
  }

  ngAfterViewChecked() {
    if (
      document.querySelector('#content') &&
      document.querySelector('.button')
    ) {
      this.heightScroll =
        document.querySelector('#content').clientHeight -
        document.querySelector('.button').clientHeight -
        180 +
        'px';
    }

    this.cdRef.detectChanges();
  }

  gotoSellingPage(page) {
    this.router.navigate(['/sales/'+page]);
  }
  gotoHome() {
    this.router.navigate(['/sales/dashboard/overview'], { state: { isSync: true } });
  }

  logout() {
    this.vhAuth.signOut().then(
      () => {
        this.vhQuerySales.closeSyncCollections();
        this.vhQuerySales.clearDefaultBranch(); //xa branchDefault trc khi thot app
        this.router.navigate(['/login']);
      },
      (error) => {
        console.error('error', error);
      }
    );
  }

  getCard(code) {
    if (code.length >= 8)
      this.vhQuerySales
        .getPaymentCard_Code_Name_Release_byCode(code)
        .then((product_codes: any) => {
          if (product_codes) {
            if (!this.product_codes.find((item) => item.code == code)) {
              if (
                this.vhQuerySales.getBranch_Management_Scope()
                  .sales_program_scope &&
                product_codes.id_branch !=
                this.vhQuerySales.getDefaultBranch()._id
              ) {
                this.vhComponent.alertMessageDesktop(
                  'error',
                  this.languageService.translate(
                    'Chi nhnh hin ti khng th s dng th ny!'
                  )
                );
                return;
              }
              let status = product_codes.status;
              if (status == 1) {
                this.vhComponent.alertMessageDesktop(
                  'error',
                  this.languageService.translate('Th cha c bn !')
                );
              } else {
                if (this.currentDate > new Date(product_codes.date_expire)) {
                  this.vhComponent.alertMessageDesktop(
                    'error',
                    this.languageService.translate('Th  ht hn !')
                  );
                } else {
                  this.handleCard(product_codes);
                }
              }
            }
            else {
              this.vhComponent.alertMessageDesktop(
                'warning',
                this.languageService.translate('ang s dng th ny!')
              );
            }

          } else {
            this.code = '';
            this.vhComponent.alertMessageDesktop(
              'error',
              this.languageService.translate('Khng tm thy th ny !')
            );
          }
        });
  }

  /**
   * get card = card.id_card
   * ly cc sn phm trong card
   * @param card product_code ca card {id_card, id_release_detail}
   */
  handleCard(card: any) {
    card.products = card.products.map((item) => {
      return {
        id_payment_card: card.id_payment_card,
        ...item,
        name: this.vhQuerySales.getlocalProduct(item.id_product).name,
        restquantity: item.quantity - item.outquantity,
        quantity_use:item.quantity == item.outquantity ? 0 : 1,
        outquantity: item.outquantity,
        stock: this.vhQuerySales.getlocalProduct(item.id_product).quantity_branch ? this.vhQuerySales.getlocalProduct(item.id_product).quantity_branch[this.vhQuerySales.getDefaultBranch()._id] : 0,
      };
    });
    this.customer = this.vhQuerySales.getlocalCustomer(card.id_customer);
    if (
      card.products.filter((item) => item.restquantity == 0).length ==
      card.products.length
    ) {
      this.modal.confirm({
        nzTitle: this.languageService.translate(' s dng ht!'),
        nzContent: this.languageService.translate(
          'Th ny  s dng ht s lng hng ha c th s dng!'
        ),
        nzOnOk: () => this.cancel(),
        nzCancelText: null,
      });
    } else {
      this.product_codes =this.product_codes.concat([card])
      for (let item of this.product_codes) {
        // check xem products c sp c lots hay ko
        if (item['products'].filter(pro => pro.ptype == 1 && pro.lots && pro.lots.length).length) {

          this.show_modal_select_lots_combo = true;
          this.tempDetail = this.tempDetail.concat(item['products'].filter(pro => pro.lots && pro.lots.length))
        }

        // check trong products c combo c lots ko
        for (let i of item['products'].filter(pro => pro.ptype == 5)) {
          if (i.combos.filter(e => e.lots && e.lots.length).length) this.tempDetail.push(i)
        }
      }

    }
  }


  handleQuantityUse(item) {
    // this.product_codes
    //   .find((prod_code) => prod_code.code == item.code)
    //   .products.find(
    //     (prod) => prod.id_product == item.id_product
    //   ).quantity_use = item.quantity_use;
  }

  //  x l i voucher v m thng bo
  exchangeVoucher() {

    if (this.product_codes.length)
      this.vhComponent.showLoading('').then(() => {
        let billDetails = this.product_codes.map((m) => {
          // bill details
          m = {
            id_payment_card: m.id_payment_card,
            id_payment_card_code: m._id,
            pctype: 1,
            quantity: 1,
            products: m.products
            .filter((item_prod) => item_prod.quantity_use)
            .map((prod) => {
              let data_add = {
                id_product: prod.id_product,
                quantity: prod.quantity_use,
                restquantity: prod.restquantity,
                outquantity: prod.outquantity + prod.quantity_use,
                price: 0,
                ptype: prod.ptype,
                ratio: prod.ratio,
              }
              if (prod.id_lotproduct) data_add['id_lotproduct'] = prod.id_lotproduct
              if (prod.id_subproduct) data_add['id_subproduct'] = prod.id_subproduct
              if (prod.ptype == 5) {
                data_add['combos'] = prod.combos.map(combo => {
                  let data_combo = {
                    id_product: combo.id_product,
                    price: combo.price,
                    quantity: combo.quantity,
                    ptype: combo.ptype,
                    ratio: combo.ratio ? combo.ratio : 1,
                  }
                  if (combo.id_lotproduct) data_combo['id_lotproduct'] = combo.id_lotproduct
                  if (combo.id_subproduct) data_combo['id_subproduct'] = combo.id_subproduct
                  return data_combo
                })
              }
              return data_add;
            }),
          };
          return m;
        });
        // bill
        let bill : VhBillType51Add= {
          bill_type: 51,
          id_employee: this.vhAuth.getUser()._id,
          id_branch: this.vhQuerySales.getDefaultBranch()._id,
          id_customer: this.product_codes[0].id_customer,
          note: '',
          date: new Date(),
          tax : 0
        };

        // this.vhComponent.hideLoading(0);
        this.vhQuerySales.createBill_Billdetail(bill, billDetails).then(
          (bill: any) => {
            this.product_codes[0].bill_code = bill.bill_code;
            this.vhComponent.hideLoading(0);
            this.vhComponent.alertMessageDesktop(
              'success',
              this.languageService.translate('S dng thnh cng!')
            );
            this.handlePrint(bill.bill_code);

          },
          (error: any) => {

          }
        );

      });
  }

  gotoChangeVoucher() {
    this.router.navigateByUrl('/sales/exchange-voucher');
  }

  handlePrint(bill_code) {
    let data = this.product_codes.map((m) => {
      // bill details
      m = {
        id_payment_card: m.id_payment_card,
        id_payment_card_code: m._id,
        code: m.code,
        name_payment_card: m.name_payment_card,
        products: m.products
          .filter((item_prod) => item_prod.quantity_use)
          .map((prod) => {
            return {
              id_product: prod.id_product,
              quantity: prod.quantity_use,
              restquantity: prod.restquantity,
              outquantity: prod.outquantity + prod.quantity_use,
              price: prod.price,
              ptype: prod.ptype,
              unit: this.getUnit(prod.units, prod.ratio),
              name: prod.name,
              ratio: prod.ratio,
              combos : prod.combos? prod.combos : [],
              id_lotproduct : prod.id_lotproduct
            };
          }),
      };
      return m;
    });
    let invoice = {
      id_employee: this.vhAuth.getUser()._id,
      id_customer: this.customer._id,
      date: new Date(),
      bill_code: bill_code,
    }

    let print = this.vhQuerySales.getLocalPrintPage('page_desktop_exchange_payment_card')
    if (print && print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)].enable) {
      let printer = print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)]
      if (printer) {
        let type = printer._id;
        this.vhComponent.showModal(this.renderPrintPage(type), { printer: printer, invoice: invoice , invoice_detail : data}, false, false, `modal-print-${type}`).then((modal) => {
          modal.onWillDismiss().then(() => this.cancel())
        })
      }
    } else {
      this.cancel()
    }
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ExchangePaymentCardK57Col3Component
      case 'print_size_k80_3c': return ExchangePaymentCardK80Col3Component
      case 'print_size_k80_4c': return ExchangePaymentCardK80Col4Component
      case 'print_size_a5': return ExchangePaymentCardA5Component
      case 'print_size_a4': return ExchangePaymentCardA4Component
      default: return ExchangePaymentCardA4Component
    }
  }

  /**
   * i th
   */
  save() {
    this.modal.confirm({
      nzTitle: 'Xc s dng th',
      nzOkText: 'Xc nhn v In',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzOnOk: () => this.exchangeVoucher(),
      nzCancelText: this.languageService.translate('Cancel'),
      nzOnCancel: () => { },
    });
  }

  /**
   * hy dng th
   */
  cancel() {
    this.tempDetail = [];
    this.list_products = [];
    this.product_codes = [];
    this.customer = null;
  }
  show_modal_choose_interface = false;
  /**
   * thit lp giao din trang bn hng
   * @param type sale-pos | sale-desktop
   */
  public updateInterface() {
    if (this.vhAuth.checkMyPermission('selling_display_page')) {
      if (!this.default_selling_page) this.show_modal_choose_interface = true;
      else
        this.vhAuth
          .updateEmployee(this.vhAuth.getUser()._id, {
            default_display_page: 'selling_display_page',
          })
          .then((bool) => {
            this.router.navigate(['/sales/'+this.default_selling_page], {
              state: { isSync: true },
            });
          });
    } else
      this.vhComponent.alertMessageDesktop(
        'error',
        this.languageService.translate('You do not have this rights')
      );
  }
  default_selling_page = this.vhAuth.getLocalMyPermissionName(
    'default_selling_page'
  );
  /**
   * thit lp giao din trang bn hng  sale-pos | sale-desktop
   *
   */
  updateSalesInterface() {
    this.vhAuth
      .updateMyPermission({
        default_selling_page: this.default_selling_page,
        default_display_page: 'selling_display_page',
      })
      .then((bool) => {
        this.router.navigate(['/sales/'+this.default_selling_page], {
          state: { isSync: true },
        });
      });
  }
  /////////////------------------------FONT_SIZE---------------------------//////////////
  public fontSize: number = parseFloat(
    localStorage.getItem('vh-sales-system-fontsize')
  );
  public setFontSize(value) {
    localStorage.setItem('vh-sales-system-fontsize', JSON.stringify(value));
    this.fontSize = value;
    document.documentElement.style.fontSize = value + 'px';
  }
  /////////////------------------------COLOR---------------------------//////////////
  changeColor(theme, event) {
    this.vhAuth.changeTheme(theme, event);
  }
  public selectLang(value: string) {
    let lang = this.langList.find(item => item.value == value)
    this.vhAuth.localStorageSET('language_code', value)
    this.vhAuth.localStorageSET('country_code', lang.country_code)
    localStorage.setItem('vh-sales-language', value);
    this.translateService.use(value);
    this.languageService.switchLanguage(value);
  }

  /**ng modal v get li thng tin user */
  closeAccountInfo(event) {
    this.user = this.vhAuth.getlocalEmployee(this.vhAuth.getUser()._id);
    this.modalAccountInfo = false;
  }

  gotoSalesVoucher() {
    this.router.navigateByUrl('/sales/sales-voucher')
  }

  gotoSalesPrepaidCard() {
    this.router.navigateByUrl('/sales/sales-payment-card')
  }


  getUnit(units: any[], ratio) {
    return this.vhQuerySales.getUnit_byRatio(units, ratio).unit
  }
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }
  tempDetail: any = []
  show_modal_select_lots_combo = false;
  handleChooseLotCombo() {
    let check = false;
    for (let i of this.tempDetail) {
      if (i.ptype == 1 && !i.id_lotproduct) check = true;
      if (i.ptype == 5 && i.combos.filter(item => item.lots && !item.id_lotproduct).length) check = true;
    }
    if (check)
      this.vhComponent.alertMessageDesktop("warning", this.languageService.translate("Please select the lot for the product!"))
    else {
      this.show_modal_select_lots_combo = false;
      // this.closeSearchProductList(this.tempDetail)
    }
  }

  gotoSalesOrder(){
    this.router.navigateByUrl('/sales/bill-web-app/sales-order')
  }
  gotoSalesOrderVoucher(){
    this.router.navigateByUrl('/sales/bill-web-app/sales-order-voucher')
  }
    gotoExchangeOrderVoucher(){
    this.router.navigateByUrl('/sales/bill-web-app/exchange-order-voucher')
  }
  gotoSalesOrderPrepaidCard(){
    this.router.navigateByUrl('/sales/bill-web-app/sales-order-payment-card')
  }
  gotoExchangeOrderCard(){
    this.router.navigateByUrl('/sales/bill-web-app/exchange-order-payment-card')
  }
}
